import React from "react";
import "./Courses.css";
import { courseData } from "../../Data/NavbarData";
import { NavLink } from "react-router-dom";
import { Link } from "react-scroll";
function Courses() {
  return (
    <>
      <div className="courses mt-5 ">
        <div className="container-fluid">
          <div className="row ">
            <div className="col-11 m-auto pb-4 bg-white shadow rounded">
            <h1
        className="text-center text-uppercase shadow py-2 m-0"
        style={{ backgroundColor: "#30b23022  ", fontSize: "50px" }}
      >
        our <span className="text-primary"> Course</span>
      </h1>
     <Link to="first" spy={true}
              smooth={true}
              offset={50}
              duration={500} ><i className="uil  uil-arrow"></i></Link>
              <div className="course-cards">
                <div className=" d-flex cards mt-4 bg-white">
                {courseData.map((data,key)=>( 
                     <div id={`${key===0?"first":""}`} className="col p-4 rounded border text-center  c-cards shadow bg-white">
                      <img src={data.image} alt="" style={{ height: "300px",width:"250px" }} />
                      <h4 className="course-name text-uppercase mt-2">{data.anker}</h4>
                  
                      <div><button className="btn  mt-2 trial btn-outline-warning">
                        Book a Demo class
                      </button></div>
                     <div> <NavLink to={`${data.path}`}  className=" btn mt-2 btn-success">Learn More</NavLink></div>
                    </div>
                    ))}
              </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Courses;
