import React from "react";
import { useState } from "react";

import "./Navbar.css";
import { Aboutdropdown, Complementry, Regular } from "../../Data/NavbarData";
import { Link } from "react-scroll";
import { NavLink } from "react-router-dom";
function Navbar() {
    const [open, setopen]=useState(false);
  return (
    <div  className="container-fluids sticky-top mx-3 ">
      <div className="row">
        <div style={{backgroundColor:"#1515ad"}} className="col-12  shadow ">
          <nav className=" navbar ">
            <a href="/#"> Navbar</a>
            <i  onClick={()=>(setopen(!open))} className="uil toggle uil-align-justify"></i>
            <ul className= {`${open===true?" line-content":"close m-0  line-content"}`} >
              <li>
                <NavLink to="/tutorweb/"> Home </NavLink>
              </li>
             
              <li
                className=" dropdownn"
                style={{ fontWeight: "500" }}
              >
              About Us
                <i className="uil drop uil-angle-down"></i>

                <ul  className="dropdown-menubar rounded bg-primary">
                
                  {Aboutdropdown.map((data,key)=>(
                   <li  key={key} className="border-bottom about-li  d-flex justify-content-between text-capitalize">
                        <Link to={`${key===0?"about":""}`}
             spy={true}
              smooth={true}
              offset={50}
              duration={500}
                         className=" ms-0 rounded text-white">
                          {data.name}
                        </Link>
                     <i className="uil down uil-angle-right"></i>
                      </li>
                      ))}
                </ul>
              </li>

              <li
                className=" dropdownn"
                style={{ fontWeight: "500" }}
              >
                Our Courses
                <i className="uil drop uil-angle-down"></i>

                <ul  className="dropdown-menubar   rounded bg-primary">
                  <div className="row  row-cols-md-2">
                    <div className="regulor p-3">
                      <h3 className="text-uppercase">Regular Courses</h3>
                     {Regular.map((data,key)=>( <li key={key} className="sub-dropdown text-capitalize">
                      < span className="list">{data.list}</span><i className="uil down uil-angle-down"></i>
                        <a className="sublist ms-5 rounded text-white" href="/">
                          {data.anker}
                        </a>
                      </li>
                      ))}
                    </div>
                    <div className="complementry p-3" style={{borderLeft:"1px solid white"}}>
                      <h3 className="text-uppercase">Complementary Courses</h3>
                     {Complementry.map((data,key)=>( <li key={key} className="sub-dropdown  text-capitalize">
                       {data.list}<i className="uil down uil-angle-down"></i>
                      <a className="sublist ms-5 rounded text-white" href="/">
                          {data.anker}
                        </a>
                      </li>
                      ))}
                    </div>
                  </div>
                </ul>
              </li>
              <li>
                <NavLink to="/tutorweb/faq"> FAQ</NavLink>
              </li>
              <li>
                <NavLink to="/tutorweb/contact"> Contact Us</NavLink>
              </li>
              <div className="d-flex btns" style={{ alignItems: "center" }}>
                <NavLink
                  to="/tutorweb/contact"
                  className="btn btn-outline-warning nav-btn me-2 font-weight-bold"
                  type="submit"
                >
                  <b> Class Join Now</b>
                </NavLink>
                <NavLink
                  to="/tutorweb/contact"
                  className="btn nav-btn trial btn-outline-light" 
                  type="submit"
                >
                  <b> Book for Free Trial</b>
                </NavLink>
              </div>
            </ul>
          </nav>
        </div>
      </div>
    </div>
  );
}

export default Navbar;
