import React from 'react'
import { Link } from 'react-router-dom'
import "./Plan.css"
function PlanCard(props) {
  return (
    <>
     <div className="batch-card">
      <div className="box card py-3 px-3 shadow">
        <h3 className="text-center text-capitalize mb-1 "> {props.type}</h3>
        <ol className="text-start plan-list ps-0 text-capitalize">
        <li>{props.live}</li>
        <li>{props.exercise}</li>
        <li>{props.record}</li>
        <li>{props.time}</li>
        <li>Rs.{props.fees}/Month</li>
       <li> <button className='btn w-100 trial btn-primary'>{props.contact}</button></li>
       <li> <button className='btn w-100 trial btn-outline-success'>{props.subscribe}</button></li>
       <li> <Link to="/tutorweb/learn" className='btn w-100 trial btn-warning'>{props.learn}</Link></li>
        </ol>
      </div>
    </div>
      
    </>
  )
}

export default PlanCard
