import React from "react";
import { Link } from "react-scroll";
import "../Learn.css";
function Navbar() {
  return (
    <div>
      <nav className="  bg-warning rounded shadow mt-3">
        <ul className=" row row-cols-6 text-center">
          <li className=" py-3">
            <Link
              to="overview"
              spy={true}
              smooth={true}
              offset={0}
              duration={300}
              className="learn-list"
              href=""
            >
              overview
            </Link>
          </li>
          <li className=" py-3">
            <Link
              to="curriculum"
              spy={true}
              smooth={true}
              offset={0}
              duration={300}
              className="learn-list"
              href=""
            >
              curriculum
            </Link>
          </li>
          <li className=" py-3">
            <Link
              to="certifiaction"
              spy={true}
              smooth={true}
              offset={0}
              duration={300}
              className="learn-list"
              href=""
            >
              certification
            </Link>
          </li>
          <li className=" py-3">
            <Link
              to="fees"
              spy={true}
              smooth={true}
              offset={0}
              duration={300}
              className="learn-list"
              href=""
            >
              fees
            </Link>
          </li>
          <li className=" py-3">
            <Link
              to="reviews"
              spy={true}
              smooth={true}
              offset={0}
              duration={300}
              className="learn-list"
              href=""
            >
              Reviews
            </Link>
          </li>
          <li className=" py-3">
            <Link
              to="faq"
              spy={true}
              smooth={true}
              offset={0}
              duration={300}
              className="learn-list"
              href=""
            >
              FAQs
            </Link>
          </li>
        </ul>
      </nav>
    </div>
  );
}

export default Navbar;
