import React from "react";
import Slider from "../Slider/Slider";
import "./Home.css";
import img1 from "../../All_image/home/Download Girl sitting at the desk, learning with computer for free.jpg";
import img2 from "../../All_image/home/Online international education isometric concept vector image on VectorStock.jpg";
import img3 from "../../All_image/home/Free Vector _ Isometric online education illustration.jpg";
import Book from "./Book";
function Home() {
  return (
    <div className="Home shadow">
            <div
              className=" py-4 ms-3"
              style={{ textTransform: "capitalize" }}
            >
              <h1  className="text-center animat">
                <span className="bg-warning text-success"> the Best online soloution</span>
                <span className="bg-warning  stroke  mt-1"> for learning recitation</span>
              </h1>
          <div className="home-content m-2 mb-5">
              <div className="m-3">  <Book/></div>
            <div className="m-3 rounded">
            <Slider
            size={"30rem"}
            img1={img1}
            img2={img2}
            img3={img3}
            />
            </div>
            </div>
          </div>
    </div>
  );
}

export default Home;
