import React from 'react'
import { Link } from 'react-router-dom'
import Classes from './Classes'

function Book() {
  return (
    <div className="">
    <div style={{backgroundColor:"#0000ff61"}} className=" rounded  text-white p-3">
      <div className="header-text mb-4">
        <h5 style={{ fontWeight: "bold" }}>
          Boook your Free Classes
        </h5>
        <div className="form-check d-flex form-switch">
          <p className="m-0 me-1"> for Kids </p>
          <input
            className="form-check-input mx-0"
            type="checkbox"
            role="switch"
            id="flexSwitchCheckDefault"
          />
        
          <span>  for 18+</span>
         
        </div>
      </div>
      <div className=" row row-cols-3 mb-5 d-flex rounded ">
        <Classes idx={1} />
      </div>
      <Link to="/contact" className="btn-warning py-3  text-light btn trial col-12 rounded-0 ">
        Schedule a Free Class
      </Link>
      </div>
    </div>
  )
}

export default Book
