import React from "react";
import "./Contect.css";
import img from "../../All_image/contact/Free Vector _ Contact us concept for landing page .png";
import Footer from "../footer/Footer";
import { Link } from "react-router-dom";
function Contact() {
  return (
    <div className="Contact ">
      <h1
        className="text-center shadow py-2 m-0"
        style={{ backgroundColor: "#30b23022  ", fontSize: "50px" }}
      >
        Contact <span className="text-success"> Us</span>
      </h1>
      <div className="container-fluids py-2 gradient">
            <h5 className="text-uppercase text-end text-primary px-3 m-0">Book for free trail demo class</h5>
        <div className="row">
          <div className="col-10 m-auto">
            <h1 className=" urdu text-center mb-5 "> ﷽ </h1> 
            <div className="row text-dark row-cols-md-2 mt-3">
              <div className="images text-center">
                <div className="d-flex justify-content-evenly">
            <li>logo</li> <li>name</li>
                </div>
                <img src={img} alt="" />
              </div>
              <div className="form mt-3">
                <div>
                  <h4 className="text-capitalize register pb-2 text-primary">
                    register new for online
                  </h4>
                  <p>
                    <i className="uil uil-angle-right mb-0"></i>Contact Us for any
                    question or queries and fill out the form bellow to request
                    a free trial.
                  </p>
                  <p>
                    <i className="uil uil-angle-right mb-0"></i>We have available
                    24/7 for the support ans services we offer.
                  </p>
                </div>
                <form className="text-capitalize " action="subimt">
                  <div className="flex-column d-flex mt-3">
                    <label htmlFor="name"> Your Name (Required)</label>
                    <input
                      className="text-capitalize bg-light"
                      type="text"
                      placeholder="e.g Your full name "
                    />
                  </div>
                  <div className="flex-column d-flex mt-3">
                    <label htmlFor="name"> Your Email id (Required)</label>
                    <input
                      className="text-capitalize bg-light"
                      type="text"
                      placeholder="e.g Your email id "
                    />
                  </div>
                  <div className="flex-column d-flex mt-3">
                    <label htmlFor="name"> Your phone (Required)</label>
                    <input
                      className="text-capitalize bg-light"
                      type="text"
                      placeholder="e.g Your Contact +00000000000 "
                    />
                  </div>
                  <div className="flex-column d-flex mt-3">
                    <label htmlFor="name"> Country Name (Required)</label>
                    <input
                      className="text-capitalize bg-light"
                      type="text"
                      placeholder="e.g Your Country Name"
                    />
                  </div>
                  <div className="flex-column d-flex mt-3">
                    <label htmlFor="name"> Country city (Required)</label>
                    <input
                      className="text-capitalize bg-light"
                      type="text"
                      placeholder="e.g Your city name"
                    />
                  </div>
                  <div className="flex-column d-flex mt-3">
                    <label htmlFor="name"> Your Message</label>
                    <textarea
                      className="bg-light rounded"
                      name="message"
                      id=""
                      cols="30"
                      rows="4"
                      placeholder="e.g You are intrested to get the services for yourself . for your kids . or anyone else . Kindly write name age and Gender of the student(s) .intrested for classes."
                    ></textarea>
                  </div>
                </form>
                <div className="declaration mt-5 row">
                  <p>
                    <i className="uil uil-angle-right mb-0"></i>By submiting your
                    conatact are agreed to the
                    <Link to="/tutorweb/contact/privacy"> terms of services and privacy policy </Link> .
                    Rest assured that we keep your personal data safe do not
                    sell or redistribute to anyone
                  </p>
                  <button className="btn col-3 m-auto trial btn-primary">
                    Submit
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
}

export default Contact;
